@import "colors";

@mixin backdrop( $primary-color, $image, $padding: 0.7em, $cover: null ) {
    position: relative;
    background-image: $image;
    background-attachment: fixed;
    background-repeat: no-repeat;
    background-size: cover;
    &>*{
        position: relative;
        z-index: 1;
    }
    @if ( $cover != max-content ){
        &::before {
            position: absolute;
            display: block;
            content: "";
            top: $padding;
            left: $padding;
            bottom: $padding;
            right: $padding;
            z-index: 0;
            @include transparent-background-color( $primary-color, 0.7 );
        }
    }
    @else {
        &>*{
            padding: $padding;
            display: block;
            margin: auto;
            width: max-content;
            @include transparent-background-color( $primary-color, 0.7 );
        }
    }
}