@import "colors";
@import "loader";
@import "mixins";
$header-font-size: 20px;
$backgroung-images: (
    img-1: url("../resources/images/apod.jpg"),
    img-2: url("../resources/images/looking_to_space.jpg"),
    img-3: url("../resources/images/launch.jpg")
);
body {
    background-color: $secondary-color-light;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    overflow-x: hidden;
    &>header {
        background-color: $primary-color-dark;
        position: fixed;
        z-index: 2;
        top: 0;
        left: 0;
        width: 100%;
        color: $font-dark;
        padding: 1rem 2rem;
        transition-duration: 0.1s;
        font-size: $header-font-size;
        h1 {
            font-size: 1em;
            transition-duration: inherit;
        }
        &[data-scroll="true"]{
            padding: 0.5rem 1rem;
            h1{
                font-size: 0.4em;
            }
        }
    }
}
*{
    margin: 0;
    box-sizing: border-box;
}

main{
    article:first-child{
        padding-top:4rem;
    }
    article {
        color: $font-dark;
        @include backdrop( $background-dark, url(".../resources/images/loading.jpg"), 1rem );
    }
    footer {
        color: $font-light;
        @include backdrop( $background-light, url(".../resources/images/loading.jpg"), 1rem );
    }
}
